section.hChat {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: calc( 100% - 12.5vw);
    overflow: hidden;
    &>div.chatInner>div.hChats {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: scroll;
        transition: left 0.4s;
        padding-top: pxtoem(60);
        &.chatShow {
            left: -100%;
            transition: left 0.4s;
        }
        &>div {
            width: 100%;
            padding: 1em;
            border-top: solid 1px dimgrey;
            cursor: pointer;
            img.chatAvatar {
                float: left;
                width: 3em;
                margin-right: 1em;
            }
            h3 {
                font-weight: bold;
                font-size: 1.1em;
            }
            p.chatTime {
                float: right;
            }
            p.chatSubline {
                span {
                    display: inline-block;
                    width: 0.7em;
                    height: 0.7em;
                    background: green;
                    border-radius: 1em;
                    margin-right: 0.4em;
                }
            }
        }
    }
    &>div.chatInner>div.hFotos {
        display: flex;
        flex-wrap: wrap;
        padding-top: pxtoem(60);
        div {
            width: 25%;
            flex-shrink: 0;
            img {
                float: left;
            }
        }
    }
    &>div.chatInner>div.hFotosSingle {
        padding-top: pxtoem(60);
    }
    &>div.chatInner>div.hChatChat {
        position: absolute;
        left: 100%;
        top: 0;
        width: 100%;
        height: 100%;
        background: turquoise;
        padding: 1em;
        transition: left 0.4s;
        overflow: scroll;
        -webkit-overflow-scrolling: touch;
        overflow-scrolling: touch;
        padding-top: pxtoem(50);
        &.showChat {
            left: 0;
            transition: left 0.4s;
        }
        &>div>div.chatTime {
            clear: both;
            float: left;
            width: 100%;
            margin: 2em 0 1em;
            text-align: center;
            font-weight: bold;
            &+div {
                margin-top: 1em !important;
            }
        }
        &>div>div.chatMessage {
            clear: both;
            float: left;
            overflow: hidden;
            max-width: 70%;
            margin-top: 0.2em;
            &>img {
                display: none;
                vertical-align: bottom;
                width: 3em;
                margin-right: 0.5em;
            }
            &>span {
                padding: 1em;
                vertical-align: bottom;
                display: inline-block;
                width: 100%;
                background: lightgray;
                border-radius: 5px;
            }
            &.chatMe {
                &>span {
                    background: lightgreen;
                }
                float: right;
                &+.chatOther {
                    margin-top: 1em;
                }
            }
            &.chatOther {
                &+.chatMe {
                    margin-top: 1em;
                }
                .chatName {
                    font-weight: bold;
                }
            }
            .chatTime {
                text-align: right;
                font-size: 0.8em;
            }
        }
    }
    // Selfie
    &#h_selfie {
        .panel {
            width: 280px;
            margin: 0 auto;
            text-align: center;
            img {
                max-width: 190px;
            }
            .form-field {
                margin: 30px 0;
                input {
                    font-size: 22px !important;
                    text-align: center;
                }
            }
            a {
                color: #e98f24;
                text-decoration: none;
                &.button {
                    color: white;
                    background-color: #e98f24;
                }
            }
        }
    }
    // SafeChat
    &#h_safeChat {
        .hChats {
            padding: unset;

            &>div {
                padding: 1em 1em 0 1em;
            }
        }

        .chatList {
            display: flex;
            justify-content: left;
            align-items: flex-start;
            border: none;
            padding-top: 1.75em;
            background-color: white;

            img {
                border-radius: 50%;
                width: 2.8em;
                height: 2.8em;
                overflow: hidden;
            }

            .chatTime {
                font-size: .85em;
                color: #859091;
                float: none;
            }

            .chatData {
                flex: 1;
                display: flex;
                justify-content: space-between;
                flex-direction: row-reverse;
            }

            .chatInfo {
                flex: 1;
                border-bottom: 1px solid #f2f2f2;
                padding-bottom: .8em;
            }

            &:last-child>div {
                border-bottom: unset;
            }
        }

        h3 {
            font-size: 1em;
            color: #2e2e2e;
        }

        .chatSubline {
            font-size: .95em;
            line-height: 1.3em;
            color: #727272;
        }

        .chatMessage {
            display: flex;

            &.chatMessage.chatMe {
                flex-direction: row-reverse;

                .messageContainer {
                    margin-right: .5em;
                    margin-left: unset;
                }
            }
        }

        .avatarContainer {
            display: none;
        }

        .messageContainer {
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-left: .5em;
        }

        .message {
            background-color: #4bd557;
            color: white;
            border-radius: 5px;
            padding: .75em 1em;
            display: flex;
            justify-content: left;
            flex-direction: column;

            .chatName {
                font-size: .9em;
                color: #4bd557;
            }
        }

        .chatText {
            margin-bottom: .2em;
        }

        .messageTime {
            font-size: .75em;
            padding: .6em 1.25em;
            text-align: left;
            color: #808c8d;
        }

        .chatMessage {
            margin-bottom: .3em;
        }

        .chatMe {
            padding-right: 1em;

            .message {
                background-color: #dcf8c6;
                color: rgb(36, 36, 36);
            }

            .messageContainer {
                align-items: flex-end;
            }
        }

        .chatOther {
            padding-left: 1em;

            .message {
                background-color: white;
                color: rgb(36, 36, 36);
            }
        }

        .chatTime {
            color: rgba(0, 0, 0, .45);
            font-size: .75em;
        }

        div.chatInner {
            .titleBar {
                background-color: #5a728f;
                color: white;
                font-weight: bold;
                text-align: left;
                padding: 16px;
                display: flex;
                justify-content: left;
                align-items: center;
                z-index: 1;
                position: sticky;

                h1 {
                    display: inline-block;
                    vertical-align: middle;
                    font-weight: bold;
                    position: static;
                    width: auto;
                    transform: none;
                    left: auto;
                    top: auto;
                    margin-left: 16px;
                    font-size: 1.2em;
                }

                img {
                    border-radius: 50%;
                    width: 2em;
                    height: 2em;
                    margin-right: 1em;
                    margin-left: .5em;
                }

                h3 {
                    color: white;
                    font-weight: bold;
                    font-size: 1em;
                }
            }

            &>div.hChatChat {
                background: linear-gradient(90deg, rgb(136, 155, 177) 0%, rgb(197, 208, 221) 100%);
                padding-top: 0;
                padding-left: 0;
                padding-right: 0;

                &>div>div.chatMessage {
                    max-width: calc(70% + 3.5em);

                    p {
                        line-height: 1.3;
                    }

                    &>img {
                        display: inline-block;
                        border-radius: 100%;
                    }

                    &>span {

                        .chatTime,
                        .chatName {
                            color: #859091;
                        }
                    }
                }
            }

            div.chatTime {
                font-size: .9em;
                font-weight: normal;
                color: #808080;
                background-color: rgb(195, 227, 255);
                padding: .5em;
                border-radius: 5px;
                margin: unset;
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                width: unset;
                margin-top: .8em;
            }
        }

        .chatPW {
            height: 100%;
            background: #5a728f;
            color: white;
        }

        .panel {
            width: 280px;
            margin: 0 auto;
            text-align: center;

            img {
                max-width: 190px;
            }

            .form-field {
                margin: 30px 0;

                input {
                    font-size: 22px !important;
                    text-align: center;
                    border-color: white;
                    background: #7791af;
                    color: white;
                }
            }

            a {
                color: white;
                text-decoration: none;

                &.button {
                    display: block;
                    color: black;
                    background-color: white;
                }
            }
        }
    }
