                    border: none;
                    text-align: center;
                    border-radius: 0;
                }
            }
            p {
                padding: 0 pxtoem(20);
                &.wrongAnswer {
                    color: #71f0ff;
                }
            }
            a {
                color: #ff70e8;
                text-decoration: none;
                &.button {
                    color: #ff70e8;
                    background-color: black;
                    border-radius: 0;
                    display: block;
                    &.games {
                        background-color: #71f0ff;
                        color: black;
                    }
                }
            }
        }
        .chatInner {
            background: black;
            color: white;
            height: 100%;
            .hChatChat {
                background: black;
                &>div {
                    &>div.chatMessage {
                        span {
                            border-radius: 0;
                        }
                        &.chatMe span {
                            background: #ff70e8;
                        }
                        &.chatOther span {
                            background-color: #71f0ff;
                            color: black;
                        }
                    }
                }
            }
        }
    }
    // SnipChat
    &#h_schnippschnapp {
        background-color: white;
        .titleBar {
            color: white;
            background-color: rgb(124, 189, 241);
            transition: .4s;
            padding-left: 1.25em;
            &.chatShow {
                transform: translateX(-100%);
            }
