                font-size: 1.1em;
            }
            img {
                margin-right: 1.25em;
                border-radius: 50%;
            }
        }
        .bg {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-color: rgb(124, 189, 241);
            z-index: -1;
        }
        .hChats {
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            overflow: hidden;
            padding-top: 0;
            margin-top: 57px;
            background-color: white;
        }
        .hChatChat {
            background-color: white;
            padding-left: 0;
            padding-right: 0;
        }
        .chatOverview {
            border-bottom: 1px solid #d6d6d6;
            border-top: none;
            img {
                position: relative;
                border-radius: 50%;
                width: 3m;
                height: 3em;
                overflow: hidden;
            }
        }
        h3 {
            margin-bottom: .2em;
            letter-spacing: .025em;
            font-weight: normal;
        }
        .chatText {
            display: flex;
            justify-content: left;
            font-size: .9em;
            color: #888888;
            .chatSubline {
                margin-right: .7em;
            }
        }
        .chatMessage {
            float: unset;
            width: 100%;
            max-width: unset;
            background-color: unset;
            background: unset;
            margin-bottom: .5em;
            margin-top: 0;
            padding: 0 1em;
            img {
                max-width: 70%;
            }
        }
        .chatContent {
            background-color: unset;
            padding-left: .8em;
            .chatText {
                color: black;
                font-size: 1em;
                font-weight: 500;
            }
        }
        .chatOther {
            .senderName {
                font-weight: bold;
                color: rgb(124, 189, 241);
                text-transform: uppercase;
                font-size: .85em;
            }
            .chatContent {
                border-left: 3px solid rgb(124, 189, 241);
            }
        }
        .chatMe {
            .senderName {
                font-weight: bold;
                color: rgb(238, 177, 10);
                text-transform: uppercase;
                font-size: .85em;
            }
            .chatContent {
                border-left: 3px solid rgb(238, 177, 10);
            }
        }
        .showChat {
            padding-top: 0;
            .chatTime {
                font-weight: normal;
                color: #888888;
                font-size: .75em;
                text-transform: uppercase;
            }
            .titleBar.show {
                background-color: rgb(124, 189, 241);
                transition: .4s;
                padding: 1.25em;
                display: flex;
                justify-content: initial;
                position: sticky;
                z-index: 1;
                &.chatShow {
                    transform: translateX(-100%);
                }
                h1 {
                    position: relative;
                    text-align: center;
                    font-size: 1.1em;
                }
                img {
                    margin-right: 1.25em;
                    border-radius: 50%;
                    display: none;
                }
                .chatName {
                    color: white;
                    text-align: left;
                    font-size: 1.1em;
                }
                span {
                    padding: 0;
                }
            }
        }
    }
