section#h_home {
    padding: 5%;
    background: url("../images/wallpaper.jpg") center;
    background-size: cover;
    height: 100%;
    .time {
        font-size: pxtoem(100);
        margin: 40px 0;
        text-align: center;
        color: white;
        line-height: 1;
        span {
            display: block;
            font-size: pxtorem(20);
        }
    }
}

.hAppIcon {
    width: calc(25% - 3.75%);
    margin: 0 0 5% 5%;
    float: left;
    &:nth-child(4n+1) {
        clear: left;
        margin-left: 0;
    }
    text-align: center;
    &.disabled {
        pointer-events: none;
        &>span {
            opacity: 0.5;
        }
    }
    &>span {
        position: relative;
        display: block;
        width: 100%;
        padding-bottom: 100%;
        background: $c_grau;
        margin-bottom: 0.5em;
        border-radius: 0.5em;
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
        overflow: hidden;
        &>span {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: calc(100% - 2em);
            height: calc(100% - 2em);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
        }
    }
    p {
        font-size: 0.8em;
        color: white;
    }
    &.whatsChat span {
        background-image: linear-gradient(#68cc55, #77e362);
        span {
            background-image: url("../images/appIcons/whatsChat.svg");
        }
    }
    &.sms span {
        background-image: linear-gradient(#1bc9e0, #55ebff);
        span {
            background-image: url("../images/appIcons/sms.svg");
        }
    }
    &.safeChat span {
        background-image: linear-gradient(#e0211b, #e76966);
        span {
            background-image: url("../images/appIcons/safechat.svg");
        }
    }
    &.selfie span {
        background-image: linear-gradient(#e0cd1b, #ed7224);
        span {
            background-image: url("../images/appIcons/selfie.svg");
        }
    }
    &.tacco span {
        background-image: linear-gradient(#c714d6, #eb93f2);
        span {
            background-image: url("../images/appIcons/tacco.svg");
        }
    }
    &.snipchat span {
        background-image: linear-gradient(#bfb800, #f6f15f);
        span {
            background-image: url("../images/appIcons/snipchat.svg");
        }
    }
    &.discord span {
        background-image: linear-gradient(#325747, #23875c);
        span {
            background-image: url("../images/appIcons/discord.svg");
        }
    }
    &.email span {
        background-image: linear-gradient(#342d6f, #5c53a7);
        span {
            background-image: url("../images/appIcons/email.svg");
        }
    }
    &.game span {
        background-image: linear-gradient(#4d4431, #736850);
        span {
            background-image: url("../images/appIcons/game.svg");
        }
    }
    &.wetter span {
        background-image: linear-gradient(#2c77e8, #5997f5);
        span {
            background-image: url("../images/appIcons/wetter.svg");
        }
    }
    &.browser span {
