#h_wetter {

    position: relative;
    text-align: center;
    padding: 20px;
    height: 100%;

    background: #5b9ccb;
    color: white;

    & > div {
        border-bottom: solid 2px white;
        & > div {
            padding: pxtoem(12) 0;
            border-top: solid 1.5px white;
            display: flex;
            align-items: center;
            img {
                width: 3em;
                margin-right: pxtoem(20);
            }
            h1, p {
                font-size: 2em;
                font-weight: normal;
                margin: 0;
            }
            p {
                margin-left: auto;
            }
        }
    }

}
