html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
input,
select {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-weight: inherit;
    font-style: inherit;
    font-size: 100%;
    vertical-align: baseline
}

:focus {
    outline: 0
}

#h_ohno {
    line-height: 1;
    color: black;
    background: white
}

#h_ohno {
    height: calc( 100% - 57px);
    width: 1%;
    min-width: 100%;
    margin: 0 auto;
    font-family: "MedievalSharp", sans-serif;
    background: black;
    color: white;
    font-size: 20px
}

#h_ohno .show {
    display: inherit
}

#h_ohno .hide {
    display: none !important
}

#h_ohno .hideMove {
    position: absolute;
    display: none;
    top: -5000px !important
}

@media (max-width: 1023px) {
    #h_ohno .clear {
        clear: both;
        display: block;
        overflow: hidden
    }
    #h_ohno .clear.nomargin {
        margin: 0 !important
    }
}

#h_ohno .center {
    text-align: center
}

#h_ohno .floatLeft {
    float: left !important
}

#h_ohno .floatRight {
    float: right !important
}

#h_ohno .ghost {
    opacity: 0.3;
    cursor: default !important
}

#h_ohno .ghost a {
    cursor: default !important
}

#h_ohno strong {
    font-weight: bold
}

#h_ohno strong.intro {
    display: block;
    font-size: 24px;
    margin-bottom: 1rem
}

#h_ohno .debug {
    display: none;
    padding: 0.3em 0.8em 0.4em;
    background: black;
    color: #00ff00;
    font-family: monospace;
    font-size: 14px
}

#h_ohno .inline-block {
    display: inline-block
}

#h_ohno .wrap {
    width: 100%
}

#h_ohno p,
#h_ohno label {
    line-height: 1.4
}

#h_ohno p.alert {
    margin: 1em 0;
    padding: 0.5em 1em;
    color: white
}

#h_ohno .noMargin {
    margin: 0
}

#h_ohno #board {
    position: relative;
    width: 100%;
    padding-bottom: 177.777%
}

#h_ohno #board canvas {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%
}

#h_ohno #score {
    width: 120px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 100px;
    bottom: 20px;
    background: white;
    color: black;
    padding: 0.5em 0 0.3em;
    font-size: 1.2em;
    text-align: center
}

#h_ohno .button {
    cursor: pointer;
    background: #9b1111;
    color: white;
    font-size: 1.2em;
    text-align: center;
    border-radius: 100px;
    padding: 0.5em 0 0.3em
}

#h_ohno .button#doFight,
#h_ohno .button#doHide {
    position: absolute;
    left: 20px;
    bottom: 20px;
    width: 100px
}

#h_ohno .button#doHide {
    background: #9b4f11;
    left: auto;
    right: 20px
}

#h_ohno .note {
    display: none;
    z-index: 2106;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 300px;
    padding: 1em 1em 0.6em;
    text-align: center;
    background: white;
    color: black;
    border-radius: 20px
}

#h_ohno .note.show {
    display: block
}

#h_ohno .note h2 {
    font-size: 1.8em;
    margin: 0 0 0.5em
}

#h_ohno .note p+p {
    margin-top: 20px
}

#h_ohno .note .button {
    display: block
}


