.titleBar {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: pxtoem(60);
    h1 {
        position: relative;
        z-index: 1;
        ;
        margin: 0;
        width: 100%;
        @extend .centerThis;
        img,
        &>span {
            display: inline-block;
            vertical-align: middle;
            span {
                display: block;
                text-align: left;
                font-size: 0.8em;
                font-weight: normal;
            }
        }
        img {
            width: 2em;
            margin-right: 0.5em;
            border-radius: 1em;
        }
    }
    span.switch {
        position: absolute;
        right: 14px;
        top: 14px;
        z-index: 2;
        img {
            width: 32px;
        }
    }
}

.panel {
    padding: 2em;
}

#navBottom {
    position: fixed;
    left: 0;
    bottom: 0;
    height: 12.5vw;
    width: 100%;
    background: black;
    z-index: 9999;

    div {
        position: relative;
        width: 50%;
        float: left;
        height: 100%;
    }

    span {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        width: 8vw;
        height: 8vw;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;

        &.home {
            background-image: url("../images/appIcons/home.svg");
        }

        &.back {
            background-image: url("../images/appIcons/back.svg");
        }
    }
}

.appTopNav {
    position: fixed;
    left: 0;
    top: -100%;
    width: 100%;
    background: white;
    padding: 1em;
    transition: top 1s;
    &.show {
        top: 0;
        transition: top 1s;
    }
}

@keyframes notification {
    0% {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.9);
        display: none;
    }
    1% {
        display: block;
    }
    100% {
        display: block;
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

@keyframes notificationhide {
    0% {
        opacity: 1;
        display: block;
        transform: translate(-50%, -50%) scale(1);
    }
    99% {
        display: block;
        transform: translate(-50%, -50%) scale(0.9);
    }
    100% {
        transform: translate(-50000px, -50%) scale(0.9);
        display: none;
        opacity: 0;
    }
}

.notification {
    left: 5000px;
    min-width: 280px;
    &.hidethis {
        left: 50%;
        animation: notificationhide 0.5s forwards;
    }
    &.show {
        left: 50%;
        animation: notification 0.5s forwards;
    }
    z-index: 2106;
    position: fixed;
    top: 50%;
    transform: translate(-50%,
    -50%);
    background: white;
    border-color: $c_grau_hell;
    box-shadow: 0 0 5px rgba(0,
    0,
    0,
    0.5);
    border-radius: 10px;
    color: black;
    text-align: center;
    p {
        padding: 1.2em 1.5em;
    }
    #notificationDismiss {
        display: flex;
        border-top: solid 1px $c_grau_hell;
        a {
            flex-grow: 1;
            padding: 0.8em 1.7em;
            color: $c_blau;
            text-decoration: none;
            &:first-child {
                border-right: solid 1px $c_grau_hell;
            }
        }
    }
}

#h_lock {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: black;
    &>div {
        position: absolute;
        left: 50%;
        top: 50%;
        width: 320px;
        height: 320px;
        transform: translate(-50%, -50%);
    }
    .lockButton {
        width: 32px;
        height: 32px;
        background: white;
        border-radius: 16px;
        position: absolute;
        left: 0;
        top: 0;
        &#lock2 {
            left: 50%;
            transform: translateX(-50%);
        }
        &#lock3 {
            left: auto;
            right: 0;
        }
        &#lock4 {
            top: 50%;
            transform: translatey(-50%);
        }
        &#lock5 {
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
        &#lock6 {
            left: auto;
            right: 0;
            top: 50%;
            transform: translatey(-50%);
        }
        &#lock7 {
            top: auto;
            bottom: 0;
        }
        &#lock8 {
            top: auto;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0);
        }
        &#lock9 {
            left: auto;
            right: 0;
            top: auto;
            bottom: 0;
        }
    }
    .lockConnect {
        position: absolute;
        left: 25%;
        top: 16px;
        width: 50%;
        height: 4px;
        background: white;
        transform: translate(-50%, -50%);
        display: none;
        &.show {
            display: block;
        }
        &#lock23,
        &#lock56,
        &#lock89 {
            left: 75%;
        }
        &#lock45,
        &#lock56 {
            top: 50%;
        }
        &#lock78,
        &#lock89 {
            top: auto;
            bottom: 16px;
        }
        &#lock14,
        &#lock47,
        &#lock25,
        &#lock58,
        &#lock36,
        &#lock69 {
            left: 16px;
            top: 25%;
            width: 4px;
            height: 50%;
        }
        &#lock47,
        &#lock58,
        &#lock69 {
            top: 75%;
        }
        &#lock25,
        &#lock58 {
            left: 50%;
        }
        &#lock36,
        &#lock69 {
            left: auto;
            right: 16px;
        }
        &#lock15,
        &#lock24 {
            left: 25%;
            top: 25%;
            width: 64%;
            height: 4px;
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &#lock24 {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
        &#lock26,
        &#lock35 {
            left: 75%;
            top: 25%;
            width: 64%;
            height: 4px;
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &#lock35 {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
        &#lock48,
        &#lock57 {
            left: 25%;
            top: 75%;
            width: 64%;
            height: 4px;
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &#lock57 {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
        &#lock59,
        &#lock68 {
            left: 75%;
            top: 75%;
            width: 64%;
            height: 4px;
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &#lock68 {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}

#h_email {
    padding: 2em;
    font-size: 1.5em;
    text-align: center;
}