/************************************************************************************************/
//
// ! Forms
//

.panel {
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate( -50%, -50% );
    p {
        margin: 0 0 0.8em;
    }
}

.form-field {
    margin: 0 0 1em;
}

label {
    font-size: 0.9em;
    margin: 0 0 0.5em;
}

textarea,
select,
input[type="email"],
input[type="password"],
input[type="text"] {
    width: 100%;
    padding: 0.6em 0.75em 0.3em;
    font-family: $ff_copy;
    background-color: #F2F2F2;
    border: 1px solid #B0B0B0;
    border-radius: 4px;
    color: $c_copy;
    option {
        color: $c_copy;
    }
}

input[type="range"] {
    width: 100%;
    font-family: $ff_copy;
    color: $c_copy;
}


input[type="checkbox"] {
    -webkit-appearance: none;
    /* Rectangle 3: */
    border: 1px solid #B0B0B0;
    width: 1.1em;
    height: 1.1em;
    cursor: pointer;
    border-radius: 4px;
    background: #F2F2F2;
}

input[type="checkbox"]:active, input[type="checkbox"]:checked:active {
    box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px 1px 3px rgba(0,0,0,0.1);
}

input[type="checkbox"]:checked {
    background: white url("../images/x.png") no-repeat center center;
    background-size: 12px 12px;
}

input[type="checkbox"] + label {
    clear: both;
    max-width: none;
    font-size: 1em;
    width: calc( 100% - 2.2em );
    float: right;
    a {
        color: $c_gelb;
        text-decoration: underline;
    }
}

p.required {
    margin-top: 0.5em;
    font-size: 13px;
    color: $c_rot;
}

.button {
    display: inline-block;
    &.block {
        display: block;
    }
    padding: 0.5em 1.2em;
    background-color: $c_gelb;
    color: black;
    text-decoration: none;
    text-align: center;
    border-radius: 5px;
}
