& {

    .container {
        max-width: 100%;
    }

    .cursor {
        cursor: pointer;
    }

    a, a:active {
        top: 0;
        cursor: pointer;
    }

    img {
        max-width: 100%;
        height: auto;
    }

    h2 {
        font-weight: normal;
    }

    .text-center {
        text-align: center;
    }

    .small {
        font-size: 0.85em;
    }

    .verySmall,
    .tiny {
        font-size: 0.7em;
    }

    .show {
        display: inherit;
    }

    .hide {
        display: none !important;
    }

    .hideMove {
        position: absolute;
        display: none;
        top: -5000px !important;
    }

    .clear {
        clear: both;
        display: block;
        overflow: hidden;
        &.nomargin {
            margin: 0 !important;
        }
    }

    .clear {
        @media (max-width: 1023px) {
            clear: both;
            display: block;
            overflow: hidden;
            &.nomargin {
                margin: 0 !important;
            }
        }
    }

    .center {
        text-align: center;
    }

    .floatLeft {
        float: left !important;
    }

    .floatRight {
        float: right !important;
    }

    .ghost {
        opacity: 0.3;
        cursor: default !important;
        a {
            cursor: default !important;
        }
    }

    .fullWidth {
        min-width: 100%;
        width: 100%;
    }

    font-family: $ff_copy;
    font-weight: 300;

    color: $c_copy;

    strong {
        font-weight: bold;
        &.intro {
            display: block;
            font-size: 24px;
            margin-bottom: 1rem;
        }
    }

    .debug {
        display: none;
        padding: 0.3em 0.8em 0.4em;
        background: black;
        color: #00ff00;
        font-family: monospace;
        font-size: 14px;
    }

    .inline-block {
        display: inline-block;
    }

    .wrap {
        width: 100%;
    }

    p,
    label {
        line-height: 1.4;
    }

    p.alert {
        margin: 1em 0;
        padding: 0.5em 1em;
        background: $c_rot;
        color: white;
    }

    label.pleaseCheck {
        color: $c_rot;
    }

    .col {
        @media (min-width: 768px) {
            float: left;
            width: calc( 50% - 10px );
            margin-left: 20px;
            &.first {
                margin: 0;
            }
        }
    }

    /* Wait for API */

    #waitForApi {
        position: fixed;;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        // background: rgba( 255, 255, 255, 0.8 ) url("../images/ajax-loader.gif") no-repeat center center;
        display: none;
        z-index: 1;
        &.show {
            display: block;
        }
    }

    /************************************************************************************************/
    //
    // ! Sections
    //

    & > section {

        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        overflow: hidden;
        z-index: 1;
        opacity: 0;
        transform: scale(0.9);
        transition: opacity 0.3s, transform 0.5s;

        box-sizing: border-box;

        @media (min-width: 1024px) {
            z-index: 2;
        }


        &.showApp {
            z-index: 2;
            display: block;
            width: 100%;
            opacity: 1;
            transform: scale(1);
            transition: opacity 0.3s, transform 0.5s;
        }

        max-width: 100%;

        p, li {
            line-height: 1.5;
        }

        h1 {
            font-weight: bold;
            font-size: 1.4em;
            margin: 1em;
            text-align: center;
        }

    }

}


// Imagewrap

.ofImageWrap {

    background-size: cover;

    text-align: center;
    padding-bottom: 100%;

    h2 {
        font-size: 22px;
        margin-top: 0;
    }

    label {
        margin: 0;
        font-size: 22px;
    }

    input[type="file"] {
        position: absolute;
        left: -50000px;
    }

    position: relative;
    width: 100%;

    .content {
        padding: 20px 10px 100%;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }

}

.footerCtrl {

    clear: both;
    padding: 2em;
    text-align: center;
    a {
        margin: 0 0.5em;
    }

}

#of_footer {

    clear: both;
    position: relative;
    z-index: 5;

    .container {
        max-width: 100%;
    }

    display: block !important;
    background: white;

    a {
        color: $c_rot;
    }
}

@function pxtorem( $px ) {
    @return ( ( $px ) / ( $fs_copy / 1px ) ) * 1rem;
}

@function pxtoem( $px ) {
    @return ( ( $px ) / ( $fs_copy / 1px ) ) * 1em;
}
