@font-face {
    font-family: "MedievalSharp";
    src: url("../fonts/MedievalSharp-Regular.ttf") format("truetype")
}

@import "scss/reset.scss";
@import "scss/swiper.scss";
@import "scss/includes.scss";
$c_bg: #FBF7F2;
$c_copy: #000;
$c_rot: #E30613;
$c_rot_dunkel: #760811;
$c_gelb: #FFCD14;
$c_hell_gelb: #F7F3E5;
$c_blau: #1E3F92;
$c_link: $c_copy;
$c_grau: #878786;
$c_grau_hell: #E5E5E5;
$c_grau_weiss: #F5F5F5;
$ff_copy: "Arial",
sans-serif;
$fs_copy: 16px;
$ff_bold: $ff_copy;
$ff_head: $ff_copy;
html {
    zoom: 100%;
    * {
        box-sizing: border-box;
    }
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

html,
body {
    min-height: 100%;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-smoothing: antialiased;
}

html {
    height: 100%;
}

body {
    height: 100%;
    width: 1%;
    min-width: 100%;
    margin: 0 auto;
    color: $c_copy;
    @media (min-width: 768px) {
        font-size: 3.2vw;
    }
}

#preloader {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 2106;
    .inner {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-image: url("%%df_img/Loader.gif");
        background-position: center top 40px;
        background-repeat: no-repeat;
    }
}

body.preloading {
    #preloader {
        display: inherit;
    }
}

.wrap {
    width: 1%;
    min-width: 100%;
    height: 100%;
    &>.container {
        min-height: 100%;
    }
}


/************************************************************************************************/

//
// ! Rahmen
//
#h {
    z-index: 1;
    display: none;
    height: 100%;
    #hWrap {
        max-width: 1200px;
    }
    &.show {
        display: block;
    }
    &.show-editor {}
    @import "scss/general.scss";
    @import "scss/000-ui.scss";
    @import "scss/010-home.scss";
    @import "scss/050-chats.scss";
    @import "scss/090-ohno.scss";
    @import "scss/999-weather.scss";
    @import "scss/forms.scss";
    margin: 0 auto;
    &.preloading {
        #preloader {
            display: block;
            background-color: rgba(255, 255, 255, 0.8) !important;
        }
    }
    .wrap {
        width: 1%;
        min-width: 100%;
        min-height: 100%;
        &>.container {
            min-height: 100%;
        }
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-family: $ff_head;
        font-weight: normal;
    }
    .top {
        &+h1,
        &+h2,
        &+h3,
        &+h4,
        &+h5,
        &+h6 {
            margin-top: 0;
        }
    }
    .neg {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {}
    }
    h2 {
        text-align: center;
    }
    p {}
    .text-center {
        text-align: center;
    }
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
    select,
    textarea,
    input {
        font-size: 16px !important;
    }
}

div.waitForApi {
    position: fixed;
    z-index: 9999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    // background: rgba(255,255,255,0.9) url("../images/ajax-loader.gif") no-repeat center center;
}

@import "scss/select2-layout.scss";
// Includes & Mixins
.centerThis {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}